define('skylark-graphics-pens/pens',[
	"skylark-langx/skylark"
],function(skylark){
	return skylark.attach("graphics.pens",{});
});
define('skylark-graphics-pens/LineGap',[
    "skylark-langx/langx",
	"./pens"
],function(langx,pens) {

	var LineGap = ["butt" ,	"round" ,"square"];

	langx.mixin(LineGap , {
		"butt" : 0,
		"round" : 1,
		"square" : 2
	});
	
	return pens.LineGap = LineGap;
});	

define('skylark-graphics-pens/LineJoin',[
    "skylark-langx/langx",
	"./pens"
],function(langx,pens) {
	var LineJoin = ["round" , "bevel" , "miter"];

	langx.mixin(LineJoin , {
		"round" : 0, 
		"bevel" : 1, 
		"miter" : 2 
	});


	return pens.LineJoin = LineJoin;
	
});	

define('skylark-graphics-pens/Pen',[
    "skylark-langx/langx",
	"skylark-graphics-brushes/Brush",
	"./pens",
	"./LineGap",
	"./LineJoin",
],function(langx,Brush,pens,LineGap,LineJoin) {

	var Pen = langx.klass({
		"klassName"	:	"Pen",

		"lineWidth" : {
			type   : Number,	
            "get" : function() {
                return this._.lineWidth;
            },
            "set" : function(v) {
                this._.lineWidth = v;
            }  
   		},

		"lineCap" : {
			type  : LineGap,
            "get" : function() {
                return this._.lineCap;
            },
            "set" : function(v) {
                this._.lineCap = v;
            }  
		},

		"lineJoin" : {
            "get" : function() {
                return this._.lineJoin;
            },
            "set" : function(v) {
                this._.lineJoin = v;
            }  
		},
		

		"miterLimit" : {
			type   : Number,			
            "get" : function() {
                return this._.miterLimit;
            },
            "set" : function(v) {
                this._.miterLimit = v;
            }  
		},

		"lineDash" : {
			type   : Number,			
            "get" : function() {
                return this._.lineDash;
            },
            "set" : function(v) {
                this._.lineDash = v;
            }  
		},

		"lineDashOffset" : {
			type   : Number,			
            "get" : function() {
                return this._.lineDashOffset;
            },
            "set" : function(v) {
                this._.lineDashOffset = v;
            }  
		},

		"strokeStyle" : {
			"type"   : Brush,			
            "get" : function() {
                return this._.strokeStyle;
            },
            "set" : function(v) {
                this._.strokeStyle = v;
            }  
		},

		"_construct" : function(params) {
			this._ = langx.colone(params);
		}

	});
	

	return pens.Pen = Pen;
});	

define('skylark-graphics-pens/main',[
	"./pens",
	"./Pen"
],function(pens){
	return pens;
});
define('skylark-graphics-pens', ['skylark-graphics-pens/main'], function (main) { return main; });

